    // SINGLE PRODUCT LAYOUT
.woocommerce {
    #content div.type-product {
        >div.images {
            overflow:hidden;
            max-width: 450px;
            display: block;
            margin: 0 auto;
            @include media-query(lap-and-up) {
                width: 52.6%;
            
            }
        }
        div.summary {
            @include media-query(lap-and-up) {
                width: 44.4%;
            }
        }
    }
}

.single-product .type-product {
    >.images {
        position: relative;
        border: 1px solid rgb(243, 226, 163);
        background-color: #fff;
        
            
        
    }
    >.summary {
        color: rgb(97, 97, 97);
        .single-product-title-wrapper,
        .single-product-cart-wrapper {
            display: inline-block;
            vertical-align: top;
        }
        .title-and-cart {}
        .single-product-title-wrapper {
            font-size: 0px;
            margin-bottom: 1rem;
            >*{ font-size:1rem;}
            >h1 {
                font-family: "bebas_neueregular";
                color: rgb(160, 160, 160);
                margin: 0px;
                font-size: 2.25rem;
                display: block;
                width: 100%;
                vertical-align: top;
                line-height: 1; 
            }
            >h2 {
                font-size: 0.75rem;
                margin: 0px;
                display: inline-block;
                width: 100%;
                vertical-align: top;
                line-height: 1; 
            }
            
        }
        .buy-section{
            padding: 0 0 1rem;
            margin: 1rem 0;
            border-bottom: 1px solid #cfad42;
        }
        .accordion-title-wrap{
            padding: 0px 5% 0px 10%;
            overflow: hidden;
            .wpmenucart-icon-shopping-cart-0{
                float:left;
            }
        }
        #buy-wrapper{
            
            overflow: hidden;
            
            p{ margin:0;}
            .price {
                
                font-weight: 400;                
               
                
                position: relative;
                background: #ede9e9;
                font-size: 18px;
                color: #5c5c5b;
                line-height: 38px;
                height: 38px;
                display: inline-block;
                &::before{
                    background-color: #CFAD42;
                    color: #fff;
                    height: 100%;
                    display: inline-block;
                    border-right: 5px solid #fff;
                    padding: 0 8px;
                    width: 44px;
                    text-align: center;
                    font-size: 30px;
                }
                >.amount{
                    display: inline-block;
                    vertical-align: top;
                    padding: 0px 5px;
                }
            }
        }
        .single-product-cart-wrapper {
            display: inline-block;
            width: 100%;
            vertical-align: top;            
            margin: 1rem 0 0;            
            >.cart {
                display: inline-block;
                vertical-align: top;
                margin: 0px;
                //padding: 12px 0 0;
                padding: 0;
                font-size: 0px;    
                width:100%;            
                >*{ font-size:1rem;}
                .variations{
                    font-size: 1rem;

                    width:auto;
                    .select_box{
                        overflow: hidden;
                        flex-wrap: wrap;
                        display: flex;
                        .select_option { margin-bottom: 3px; }
                    }
                    .reset_variations{
                        display: inline-block;
                        font-size: 1rem;
                        line-height: 1;
                        width: 52%;
                        color:#000;
                    }
                }
                .single_variation_wrap{
                    display: flex;
                    justify-content: space-between;
                    
                    
                    .woocommerce-variation-add-to-cart{
                        
                        margin: 0px;
                        
                    }
                    .woocommerce-variation{display:none !important;}
                }
            }
            >.wc_quick_buy_form{
                display: inline-block;
                vertical-align: top;
                
            }
            .variation-form-title{
                font-family: bebas_neueregular;
                color: rgb(160, 160, 160);
                margin: 0px 0px 1rem;
                font-size: 1.5rem;
            }
        }
        .quantity .qty {
            width: 2.631em;
            text-align: center;
            font-size: 1.418em;
            height: 38px;
        }
        .single_add_to_cart_button {
            background-color: #CFAD42;
            height: 38px;
        }
        .social-wrap {
            border-bottom: 1px solid rgb(207, 173, 66);
            >h5 {
                text-align: left;
                font-size: 0.85rem;
                margin: 0px 0px 0.5rem;
            }
        }
        .shop_attributes,
        [itemprop="description"] {
            border-bottom: 1px solid #cfad42;
        }
        [itemprop="description"] {
            margin: 0 0 1rem;
            >p{ margin: 1rem 0;}
        }
    }
}
.single-product .product-type-simple {
    & > .summary .single-product-cart-wrapper > .cart{
        width:100%;
        display: flex;
        justify-content: space-between;
    }
}
.woocommerce {
    table.shop_attributes {
        text-align: left;
        font-size: 0.875rem;
        margin: 0;
        &,
        td,
        th {
            border-top: none;
            border-bottom: none;
            vertical-align: top;
            padding-right: 1.5rem;
            padding-bottom: .75rem;
            font-size: 1rem
        }
        .alt td,
        .alt th {
            background: none;
        }
        th {
            padding: 0;
            width: 69px;
            padding-right: 2.5rem;
            font-size: 1rem;
        }
        td p {
            padding: 0 0 1rem;
        }

        .size-wrap{
            display:inline-block;
            margin-right: 5px;
            p{ 
                display:inline-block;
                font-size: 0.9rem;
                padding: 0 6px;
            }
            >img{
                margin: 0px auto;
                display: block;
            }
        }
    }
    .product .social {
        display: inline-block;
        margin: 0 0 0.5rem !important;
        >span {
            display: inline-block;
            vertical-align: top;
        }
        .fb-icon {
            width: 24px;
            height: 34px;
        }
        .twitter-icon {
            height: 34px;
            width: 34px;
        }
    }
}

.variations_form>.variations {
    .pa_color{
      //display: none;  
    }
}

.pp_hoverContainer,
div.ppt,
.pp_gallery {
    display: none !important;
}

.woocommerce #respond input#submit.alt,.woocommerce a.button.alt,.woocommerce button.button.alt,.woocommerce input.button.alt {
    background-color:#CFAD42;;
    color:#fff;
    -webkit-font-smoothing:antialiased
}
.woocommerce #respond input#submit.alt:hover,.woocommerce a.button.alt:hover,.woocommerce button.button.alt:hover,.woocommerce input.button.alt:hover {
    background-color:#A17D54;
    color:#fff
}

.yith_wccl_value.double{
    height:13.5px;
    &:first-child{
        border-bottom:none;
    }
    &:nth-child(2){
        border-top:none;
    }
}
.select_box_image{
    .select_option_image{
        text-align: center;
        display: inline-block;
        padding: 5px;
        width:68px;
        &::before{
            content:attr(data-value);
            display: block;
            font-size: 0.75rem;
            line-height: 1;
        }
        .yith_wccl_value{
            height: auto;
            display: block;
            margin: 0px auto;            
        }
        
    }
}