/*------------------------------------*    #ALIASES
\*------------------------------------*/
html {
  box-sizing: border-box; }

body {
  background-color: #fff;
  font-family: 'Georgia','Antic Didone', serif;
  background-image: url("../images/bg.png"); }

body.admin-bar .page-wrapper {
  margin-top: 32px !important; }

@media screen and (max-width: 782px) {
  body.admin-bar .page-wrapper {
    margin-top: 46px !important; } }

a:visited {
  color: initial; }

.hide-text {
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden; }

input[type=number] {
  -moz-appearance: textfield; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

svg path {
  fill: inherit; }

*:link {
  color: #000; }

/* PAGE */
.wrap.container > .content {
  background-color: #FFF; }

#container {
  padding: 20px 12px;
  overflow: hidden; }

.page-wrapper {
  padding: 0;
  overflow: hidden;
  position: relative;
  max-width: 1280px;
  margin: 0 auto;
  box-shadow: 0px 0px 95px -22px rgba(84, 84, 84, 0.83); }

.page-wrapper--little {
  max-width: 930px; }

@media screen and (max-width: 49.9375em) {
  .home .page-wrapper {
    padding: 0; } }

main > .woocommerce {
  padding: 1rem 2%; }

